/* base */

*,
*:before,
*:after {
  box-sizing: border-box;
}  

body {
  // -webkit-tap-highlight-color: $selection-color
}

// Basic Styles
html {
  font-size: inherit;
  line-height: 1.3;
  font-size: 16px;
}

html,
body {
  height: 100%;
  padding: 0;
  margin: 0;
  min-width: 320px;
}  

body {
  background: $color-white;
  font-size: 16px;
  font-family: $primary-font;
  color: $primary-color;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
}

//  Typography
.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  color: $primary-color;
  font-weight: bold;
  text-decoration: none;
  margin: 0 0 15px 0;
}

.h1,
h1 {
  font-size: 21px;
  line-height: 1.05;
  letter-spacing: -1px;
  margin-bottom: 25px;
}

.h2,
h2 {
  font-size: 18px;
  line-height: 1.14;
  margin-bottom: 20px;
}

.h3,
h3 {
  font-size: 16px;
  line-height: 1.38;
}

.h4,
h4 {
  font-size: 14px;
  line-height: 1.38;
}

.h5,
h5 {
  font-size: 13px;
}

.h6,
h6 {
  font-size: 12px;
}

.small,
small {
  font-size: 12px;
  line-height: 1.2;
}

p {
  line-height: 22px;
  margin: 0 0 20px 0;
}

em,
i,
.em,
.i {
  font-style: italic;
}

strong,
b,
.strong,
.b {
  font-weight: bold;
}

hr {
  border: solid #ddd;
  border-width: 1px 0 0;
  clear: both;
  margin: 10px 0 30px;
  height: 0;
}

// Links
a,
.link {
  color: $link-color;
  text-decoration: underline;
  outline: none;
  transition: color 0.35s, background 0.35s, border 0.35s;
  &:hover,
  &:focus,
  &:active {
    color: $link-color;
    text-decoration: none;
    outline: none;
  }
}

a.link {
  &:hover,
  &:focus,
  &:active {
    text-decoration: none;
  }
}

img {
  box-sizing: content-box;
  display: inline-block;
  vertical-align: middle;
}

.container {
  margin: 0 auto;
  // width: 1280px;
  width: 100%;
  min-width: 320px;
  padding: 0 15px;
}

/* text align */
.text {
  &-center,
  &--center {
    text-align: center !important;
  }
  &-left,
  &--left {
    text-align: left !important;
  }
  &-right,
  &--right {
    text-align: right !important;
  }
}

/* loader */
.loader {
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
  position: fixed;
  z-index: 10000;
  background-color: $color-white;
  display: flex;
  align-items: center;
  justify-content: center;
}
  
@keyframes bouncing-loader {
  to {
    opacity: 0.1;
    transform: translate3d(0, -1rem, 0);
  }
}

.bouncing-loader {
  display: flex;
  justify-content: center;
  &>div {
    width: 1rem;
    height: 1rem;
    margin: 3rem 0.2rem;
    background: #8385aa;
    border-radius: 50%;
    animation: bouncing-loader 0.6s infinite alternate;
    &:nth-child(2) {
      animation-delay: 0.2s;
    }
    &:nth-child(3) {
      animation-delay: 0.4s;
    }
  }
}

/* img */
.img {
  &-responsive,
  &--responsive {
    display: block;
    height: auto;
    max-width: 100%;
  }
  &-round,
  &--round {
    border-radius: 50%;
  }
}

/* section */
.section {
  padding: 50px 0;
  min-width: 320px;
}

.container {
  .section {
    min-width: inherit;
  }
}

ul li {
  list-style-type: disc;
}
ol li {
  list-style-type: decimal;
}

/* bg */
.bg {
  &--pink {
    background-color: pink !important;
  }
  &--white {
    background-color: $color-white !important;
  }
  &--black {
    background-color: $color-black !important;
  }
}

/* no padding no margin */
.no {
  &--p {
      padding: 0 !important;
    }
    &-top {
      padding-top: 0 !important;
    }
    &-bottom {
      padding-bottom: 0 !important;
    }
    &-left {
      padding-left: 0 !important;
    }
    &-right {
      padding-right: 0 !important;
    }
  &--m {
      margin: 0 !important;
    }
    &-top {
      margin-top: 0 !important;
    }
    &-bottom {
      margin-bottom: 0 !important;
    }
    &-left {
      margin-left: 0 !important;
    }
    &-right {
      margin-right: 0 !important;
    }
  &--b {
      background: none !important;
    }
    &-image {
      background-image: none !important;
    }
  &--border {
    border: 0 !important;
  }
}

/* icons */
.icon {
  display: inline-block;
  // &--sprite
  //   @include sprite($img-name)
}

/* color */
.color {
  &--black {
    color: $color-black !important;
  }
  &--white {
    color: $color-white !important;
  }
}